@font-face {
    font-family: 'VC';
    src: url('../fonts/VC/ViceCity.ttf');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'VC-italic';
    src: url('../fonts/VC/RageItalic.ttf');
    font-weight: normal;
    font-style: normal;
}
* {
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.VC-bg{
    background: url('../img/VC/background.jpg') center/cover no-repeat;
    clip-path: polygon(0% 1%, 96% 9%, 100% 96%, 4% 99%);
    min-height: 100vh;
}
.VC-block{
    padding: 10%;
}
.VC{
    background: black;
    font-family: 'VC';
    color: #ff96ff;
    text-shadow: 4px 3px black;
}
.vc-logo{
    width: 15%;
}
.vc-menu{
    font-size: 44px;
    text-align: center;
    cursor: pointer;
    position: relative;
    display: block;
    z-index: 5;
    margin: 10px 0;
    display: flex;
    justify-content: space-between;
    color: #ff96ff;
    text-decoration: unset;
}
.vc-menu.link{
    margin: 10px auto;
}
.vc-menu-block {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 55%;
    margin: 0 auto;
}
.vc-menu-bg{
    content: '';
    display: block;
    position: absolute;
    z-index: -1;
    left: -7%;
    top: 0;
    width: 120%;
    height: 25%;
    background: green;
    transition: top .15s, opacity .7s;
    clip-path: polygon(4% 10%, 96% 10%, 91% 90%, 0% 91%);
}
.volume-slider{
    display: flex;
    align-items: center;
}
.volume-slider .outside-volume {
    width: 10px;
    height: 50%;
    transition: .1s all;
    display: flex;
    align-items: end;
}
.volume-slider .inside-volume{
    width: 50%;
    height: 50%;
    background-color: #174d6a;
    box-shadow: 4px 3px black;
}
.volume-slider .outside-volume:nth-child(2) .inside-volume{height: calc(100% - 15px);}
.volume-slider .outside-volume:nth-child(3) .inside-volume{height: calc(100% - 14px);}
.volume-slider .outside-volume:nth-child(4) .inside-volume{height: calc(100% - 13px);}
.volume-slider .outside-volume:nth-child(5) .inside-volume{height: calc(100% - 12px);}
.volume-slider .outside-volume:nth-child(6) .inside-volume{height: calc(100% - 11px);}
.volume-slider .outside-volume:nth-child(7) .inside-volume{height: calc(100% - 10px);}
.volume-slider .outside-volume:nth-child(8) .inside-volume{height: calc(100% - 9px);}
.volume-slider .outside-volume:nth-child(9) .inside-volume{height: calc(100% - 8px);}
.volume-slider .outside-volume:nth-child(10) .inside-volume{height: calc(100% - 7px);}
.volume-slider .outside-volume:nth-child(11) .inside-volume{height: calc(100% - 6px);}
.volume-slider .outside-volume:nth-child(12) .inside-volume{height: calc(100% - 5px);}
.volume-slider .outside-volume:nth-child(13) .inside-volume{height: calc(100% - 4px);}
.volume-slider .outside-volume:nth-child(14) .inside-volume{height: calc(100% - 3px);}
.volume-slider .outside-volume:nth-child(15) .inside-volume{height: calc(100% - 2px);}
.volume-slider .outside-volume:nth-child(16) .inside-volume{height: calc(100% - 1px);}
.volume-slider .outside-volume:nth-child(17) .inside-volume{height: 100%;}
.volume-slider .inside-volume.active{
    background-color: #62c3f7;
}
.not-visible{
    opacity: 0;
}
@media(max-width: 1200px){
    .vc-logo{
        margin: 0 auto 10%;
        display: block;
        width: 30%;
    }
    .vc-menu-block {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 70%;
    }
    .VC-bg {
        clip-path: polygon(2% 2%, 97% 5%, 95% 96%, 7% 98%);
    }
}
@media(max-width: 600px){
    .vc-logo{
        width: 55%;
    }
    .vc-menu-block {
        width: 95%;
    }
    .vc-menu.link.back {
        top: 60px;
    }
}
@media(max-width: 450px){
    .vc-menu-block {
        width: 99%;
    }
    .VC-block {
        padding: 10% 4%;
    }
    .VC-bg {
        clip-path: unset;
    }
    .vc-menu {
        flex-direction: column;
        align-items: center;
    }
    .volume-slider {
        height: 35px;
    }
    .vc-menu div{
        margin: 10px 0;
    }
    .volume-slider,.volume-slider .outside-volume {
        margin: 0;
    }
    .volume-slider .outside-volume {
        width: 10px;
        height: 80%;
    }
    .radio-select div{
        margin: 0;
    }
    .vc-menu-bg {
        left: 0%;
        width: 100%;
    }
}